/* الأنماط العامة */
body {
    margin: 0;
    font-family: Arial, sans-serif;
}

.app-container {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

/* الصف العلوي الثابت */
.top-bar {
    height: 7vh;
    background-color: #007bff;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

/* المحتوى الرئيسي */
.main-content {
    flex: 1;
    padding: 20px;
    margin-top: 7vh;
    text-align: center;
}

/* الصف السفلي الثابت */
.bottom-bar {
    height: 10vh;
    background-color: #f8f9fa;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    bottom: 0;
    width: 100%;
    box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.1);
}

/* الأقسام داخل الصف السفلي */
.section {
    width: 16%;
    text-align: center;
    cursor: pointer;
}

.section p {
    margin: 5px 0;
    font-size: 12px;
}
